import Vue from 'vue'
import Vuex from 'vuex'


Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    id:0,
  },
  mutations: {

    increment (state, n) {
      state.id += n
    },
    reset (state){
      state.id = 0
    },
    decrement (state, n) {
      state.id -= n
    },
  },
  actions: {

  }
})
