<template>
<!-- 	v-on:keyup.107="MathOp($event, '+')" v-on:keyup.109="MathOp($event, '+')" v-on:keyup.106="MathOp($event, '+')" v-on:keyup.111="MathOp($event, '+')" -->
  <input type="text" @keyup.enter="$event.target.blur(), mathOp($event)" @keyup.esc="$event.target.blur()"
	 	v-model="displayValue" @blur="isInputActive = false" @focus="isInputActive = true"
		 v-on:keyup.107="checkOp($event)" v-on:keyup.109="checkOp($event)" v-on:keyup.106="checkOp($event)" v-on:keyup.111="checkOp($event)"
		class="form-control" :placeholder="currency + 0.00"/>
</template>

<script>
export default {
	name:'currency-input',
	props: ["value",
	"currency"],
	data: function() {
		return {
			isInputActive: false
		}
	},
	computed: {
    displayValue: {
      get: function() {
				if (this.isInputActive) {
					// Cursor is inside the input field. unformat display value for user but keep number fixed to 2 decimal places only
					return this.value.toString()
				} else {
					// User is not modifying now. Format display value for user interface
					return this.currency + this.value.toFixed(2).replace(/(\d)(?=(\d{3})+(?:\.\d+)?$)/g, "$1,")
				}
      },
      set: function(modifiedValue) {
				//make sure they are not writing an expression
				if(modifiedValue.indexOf('+') !=-1 || modifiedValue.indexOf('-') !=-1 ||modifiedValue.indexOf('*') !=-1 ||modifiedValue.indexOf('/') !=-1){
					console.log("it's an expression");
				}
				else{//if it's not an algebric expression the do the below code
					// Recalculate value after ignoring "$" and "," in user input
					let newValue = parseFloat(modifiedValue.replace(/[^\d\.]/g, ""))
					
					// Ensure that it is not NaN
					if (isNaN(newValue)) {
						newValue = 0
					}
					// Note: we cannot set this.value as it is a "prop". It needs to be passed to parent component
					// $emit the event so that parent component gets it
					this.$emit('input', newValue)
				}
      }
		},
	},
	methods:{
		checkOp(e){//don't allow users to input more than one operator between every two numbers
			if(e.target.value[e.target.value.length-2] === '+' || e.target.value[e.target.value.length-2] === '-' || e.target.value[e.target.value.length-2] === '*' || e.target.value[e.target.value.length-2] === '/'){
				e.target.value = e.target.value.slice(0, -1);
			}
		},
		mathOp(e){//emit the eval value of the string expression
			if(/\d/.test(e.target.value)){//make sure it's a number and not just an operator
				this.$emit('input', eval(e.target.value))
			}
			
		}
	}
}
</script>