<template>
<div>
	<div class="tracker avc">
    <div v-if="marked_array.length == 0" class="alert mb-0 h-100 text-center" role="alert">
      <p class="proejction">You need to mark complete dates in the tracker to use this feature</p>
    </div>
    <div v-if="marked_array.length >0" class="table-responsive w-auto table-wrapper text-nowrap float-left">
      <table class="table text-center table-striped">
        <thead class="thead-light">
          <tr>
            <th scope="col" width="15%">Since</th>
            <th scope="col" width="15%">As Of</th>
            <th scope="col" width="15%">Started with</th>
            <th scope="col" width="15%">Total income made</th>
            <th scope="col" width="15%">Total spent</th>
            <th scope="col" width="15%">What you have</th>
          </tr>
        </thead>
        <tbody>
          <tr class="table-light">
            <td>{{tracked_data[0].pay_date}}</td>
            <td>{{last_marked}}</td>
            <td>{{formatMoney(user_object.money.starting_savings)}}</td> 
            <td>{{formatMoney(total_income)}}</td>
            <td>{{formatMoney(total_spent)}}</td>
            <td v-if="actual_savings>=0">{{formatMoney(actual_savings)}}</td>
            <td class="text-danger" v-if="actual_savings<0">You have no savings!</td>
          </tr>
        </tbody>
      </table>
      <table class="table text-center table-striped">
        <thead class="thead-light">
          <tr>
            <th scope="col" width="15%">Number of pay periods</th>
            <th scope="col" width="15%">Fixed monthly payments</th>
            <th scope="col" width="15%">Living expenses</th>
            <!-- <th v-if="date_interval == 14" scope="col" width="15%">Average spendings biweekly</th> -->
            <th scope="col" width="15%">Average spendings per week</th>
            <!-- <th v-if="date_interval > 14" scope="col" width="15%">Average spendings per month</th> -->
          </tr>
        </thead>
        <tbody>
          <tr class="table-light">
            <td>{{marked_array.length}}</td>
            <td>{{formatMoney(monthly_expenses)}}</td>
            <td>{{formatMoney(living_expenses -monthly_expenses-total_ote)}}</td>
            <td v-if="date_interval == 14">{{formatMoney(((living_expenses-monthly_expenses-total_ote)/marked_array.length/2))}}</td>
            <td v-if="date_interval == 7">{{formatMoney(((living_expenses-monthly_expenses-total_ote)/marked_array.length))}}</td>
            <td v-if="date_interval > 14">{{formatMoney(((living_expenses-monthly_expenses-total_ote)/marked_array.length/4))}}</td>
          </tr>
        </tbody>
      </table>
    </div>

    <div :class="{hide: show_ote}" class="row mb-3">
      <div class="col-md-12">
         <p>Add one time expenses?</p><button @click="addOTExpenses()" class="btn btn-secondary">Yes</button>
      </div>
    </div>
    
    <div v-if="show_ote" class="row">
      <div class="col-md-4">
        <p class="mb-2">Add one time expenses below to get a more realistic average spendings</p>
        <ul class="list-group text-dark input-group input-group">
          <li v-for="(row, index) in ot_expenses" :key="index" 
              class="list-group-item d-flex justify-content-between align-items-center pr-2 pl-2">
            <div v-if="row.editing" class="w-100 h-100 d-flex">
              <strong class="mr-1 text-dark">{{index+1}}- </strong>
              <input type="text" v-model="row.details" class="form-control h-100 w-75 mr-1" placeholder="Expense details">
              <Currencyinput v-model="row.amount" class="form-control h-100 w-25" :currency="currency"></Currencyinput>
              <span @click="saveOTE(row, index)" class="badge-btn badge badge-dark badge-pill ml-2 p-2"><i class="fa fa-check"></i></span>
            </div>

            <div v-if="!row.editing" class="w-100 h-100 d-flex">
              <strong class="mr-1 text-dark">{{index+1}}- </strong>
              <span class="w-75 pl-2">{{row.details}}</span> <span class="w-25 pl-2">{{formatMoney(row.amount)}}</span>
              <!-- <span @click="editOTE(row, index)" class="badge-btn badge badge-primary badge-pill ml-2 p-2"><i class="fa fa-pencil"></i></span> -->
              <span @click="deleteOTE(row, index)" class="badge-btn badge badge-danger badge-pill ml-1 p-2"><i class="fa fa-remove"></i></span>
              <button v-if="ot_expenses.length-1 == index" @click="addOTE()" class="add-ot btn btn-secondary"> <i class="fa fa-plus"></i></button>
            </div>
          </li>
        </ul>
      </div>
    </div>
	</div>
</div>
</template>

<script>
// @ is an alias to /src
import Currencyinput from '@/components/CurrencyInput.vue'
import db from './firebaseInit'
export default {
	components: {
		Currencyinput,
	},
	name:'average-spendings',
	props:[
    "tracked_data",
    "user_object"
	],
	data() {
		return {
      today: this.formatDate(new Date()),
      currency: this.user_object.money.currency,
      date_interval: this.user_object.dates.date_interval, // the frequency of payment, 14 days means biweekly
			//----------------------------------------------
			//---------------------------UTILITY--------------
      total_income: 0,
      total_spent: 0,
      living_expenses:0,
      actual_savings: 0,
      show_ote:false,
      ot_expenses:this.user_object.ot_expenses,
		}
  },
	computed:{
    total_ote(){
      var result = 0;
      for (let i = 0; i < this.ot_expenses.length; i++) {
        result += this.ot_expenses[i].amount;
      }
      console.log(result);
      return result;
    },
    monthly_expenses(){
      var month_diff = this.$moment(this.last_marked).diff(this.tracked_data[0].pay_date, 'months')+1; // get difference in months
      return month_diff * (this.user_object.money.monthly_payments+this.user_object.money.rent);
    },
    marked_array(){
      var result = [];
      for (let i = 0; i < this.tracked_data.length; i++) {
        if(this.tracked_data[i].complete){
          this.total_spent +=this.tracked_data[i].spendings;
          this.total_income +=this.tracked_data[i].income;
          result.push(this.tracked_data[i]);
        }
      }
      this.living_expenses = this.total_spent;
      return result;
    },
    last_marked(){
      if(this.marked_array.length > 0){
        this.actual_savings = this.marked_array[this.marked_array.length-1].actual_savings;

        return this.marked_array[this.marked_array.length-1].pay_date;
      }
      else {
        return 'No marked dates'
      }
    }
	},
	methods:{
    addOTExpenses(){
      this.show_ote=true;
      this.ot_expenses=[
        {
          details:'',
          amount: 0,
          editing:true
        }
      ];
    },
    saveOTE(row, id){
      if(row.details.trim().length>0){
        this.ot_expenses[id] = {
          details:row.details,
          amount:parseFloat(row.amount),
          editing:false
        };

        //this emits an event telling parent componenet "Home.vue" to receive tracked data, so it can be sent to MajorInput componenet so it is sent back in case it's in edit mode
        this.$emit('ot_created', this.ot_expenses);
    
        // this.user_object.ot_expenses = this.ot_expenses;
        this.$forceUpdate();
      }
    },
    editOTE(row, id){
      this.ot_expenses[id].editing = true;
      console.log(this.ot_expenses[id]);
      this.$forceUpdate();
    },
    deleteOTE(row, id){
      this.ot_expenses.splice(id,1);
      if(this.ot_expenses.length==0){
        this.show_ote=false;
      }
      this.$forceUpdate();
    },
    addOTE(){
      var obj = {
        details:'',
        amount: 0,
        editing:true
      };

      this.ot_expenses.push(obj);

    },
    formatDate(date){
			if(date){
				if(Array.isArray(date)){
					for(var a=0;a<date.length;a++){
						date[a] = this.$moment(date[a]).format('DD/MMM/YYYY');
					}
					return date;
				}
				else{
					return this.$moment(date).format('DD/MMM/YYYY');
				}
			}
			else{
				console.log(date, " no end date has been selected");
			}
    },
    formatMoney(money){
      return this.currency + money.toFixed(2).replace(/\d(?=(\d{3})+\.)/g, '$&,')
    }
  }
}
</script>









