<template>
<div>
	<div class="tracker">
		<div class="test hide">
			//test Today: {{today}}<br>
			<button @click="test(1)" class="mr-2">Add day to today</button>
			<button @click="test(2)" class="">Reset today</button><br>
			<!-- <button @click="lastPayAmount()" class="">function Test</button> -->
		</div>
		<div class="row mb-4 pl-3 pr-3">
			<div class="col-md-3 pl-0">
				<div class="alert h-100 pl-0" role="alert">
					<h3 class="alert-heading">Current pay period</h3>
					<p>From: <strong>{{current_pp.start}}</strong></p>
					<p class="mb-0">To: <strong>{{current_pp.end}}</strong></p>
				</div>
			</div>
			<div class="col-md-3">
				<div class="alert h-100" role="alert">
					<h3 v-if="!payday_isToday">{{days_till}} left until next pay</h3>
					<h3 v-if="payday_isToday" class="text-success">TODAY IS A PAY DAY!</h3>
				</div>
			</div>
			<div class="col-md-5">
				<div class="alert mb-0 h-100" role="alert">
					<p class="proejction">You are projected to have <span class="text-warning proejction">{{currency}}{{projected_savings.toFixed(2).replace(/\d(?=(\d{3})+\.)/g, '$&,')}}</span> in savings by {{end_date}}</p>
				</div>
			</div>
			<div class="col-md-1">
				<div class="alert h-100 controls" role="alert">
					<div class="row">
						<div class="col-md-6">
							<!-- <button :disabled="state_id==0" class="btn btn-secondary mb-3 p-2" @click="resetData()">Reset</button> -->
							<button class="btn btn-secondary mb-3 p-2" @click="restart()">Restart</button>
						</div>
						<div class="col-md-6">
							<button class="btn btn-secondary mb-3 p-2" @click="initateEdit()">Edit</button>
						</div>
					</div>
					<!-- <div class="row">
						<div class="col-md-6">
							<button :disabled="changes.length < 2" class="btn btn-secondary mb-3 p-2" @click="unDo()">Undo</button>
						</div>
						<div class="col-md-6">
							<button :disabled="state_id ==changes.length-1" class="btn btn-secondary mb-3 p-2" @click="reDo()">Redo</button>
						</div>
					</div> -->
				</div>
			</div>
		</div>
		
		<div class="row">
			<div class="col-md-2 offset-md-5">
				<div class="input-group mb-3">
					<div class="input-group-prepend">
						<span class="input-group-text" id="inputGroup-sizing-default"><i class="fa fa-search"></i></span>
					</div>

					<input @keyup="filterData(filter_term)" @keyup.esc="removeFilter(filter_term)" :disabled="!tab_isActive[0]"
					v-model="filter_term" type="text" class="form-control" aria-label="Default" 
					aria-describedby="inputGroup-sizing-default">

					<div class="input-group-append" v-if="filter_term !==''">
						<button @click="removeFilter(filter_term)"  class="btn btn-secondary input-group-text" id="inputGroup-sizing-default"><i class="fa fa-remove"></i></button>
					</div>
				</div>
			</div>
			<div class="col-md-1">
				<span v-if="no_results" class="text-danger"><strong>No results found</strong></span>
			</div>
		</div>

		<div class="row">
			<div class="col-md-12">
				<ul class="nav nav-tabs">
					<li class="nav-item">
						<a @click="switchTab(0)" v-bind:class="{'active': tab_isActive[0]}" class="nav-link">Tracker table</a>
					</li>
					<li class="nav-item">
						<a @click="switchTab(1)" class="nav-link disabled" v-bind:class="{'active': tab_isActive[1]}">Average spendings</a>
					</li>
					<li class="nav-item">
						<a @click="switchTab(2)" v-bind:class="{'active': tab_isActive[2]}" class="nav-link disabled">Graph</a>
					</li>
				</ul>

				<div class="project_ended float-left" v-if="project_hasEnded">
					<div>
						This project ended on {{end_date}} <br>
						<span v-if="!last_pay_passed">Next and last pay day is on {{next_paydate}}</span>
						<span v-if="last_pay_passed">No more payments. Last pay day was on {{date_array[date_array.length-1]}}</span>
					</div>
				</div>
				
				<div v-if="tab_isActive[0]" class="table-responsive w-auto table-wrapper text-nowrap float-left">
					<table class="table text-center table-striped">
						<thead class="thead-light">
							<tr>
								<th scope="col" width="2%">#</th>
								<th scope="col" width="8%">Pay period start</th>
								<th scope="col" width="8%">Pay period end</th>
								<th scope="col" width="8%">Pay date</th>
								<th scope="col" width="10%">Earinings on pay date <span class="info_block" @click="giveDetails('You can edit the fields in this column. You can write in an arithmetic expression and it will evaluate! (e.g (500+50)*2)')">?</span></th>
								<th scope="col" width="10%">Spendings before pay date</th>
								<th scope="col" width="14%">Savings as of each pay date (expected) <span class="info_block" @click="giveDetails('You can add notes to the fields in this column')">?</span></th>
								<th scope="col" width="14%">Savings as of each pay date (actual) <span class="info_block" @click="giveDetails('You can edit the fields in this column. You can write in an arithmetic expression and it will evaluate! (e.g (500+50)*2)')">?</span></th>
								<th scope="col" width="8%">RRSP (expected)</th>
								<th scope="col" width="8%">RRSP (actual) <span class="info_block" @click="giveDetails('You can edit the fields in this column. You can write in an arithmetic expression and it will evaluate! (e.g (500+50)*2)')">?</span></th>
								<!-- <th scope="col" width="10%" class="">Note</th> -->
								<th scope="col" width="10%"><button class="btn btn-secondary p-1" @click="markComplete(-1)">{{mark_all.text}}</button></th>
							</tr>
						</thead>
						<tbody>
							<!-- tracked_data.slice(single_view.start, single_view.end) -->
							<tr v-for="(row, index) in dataView()" :key="index" class="table-light" 
								v-bind:class="{'table-light':!row.complete,'table-dark':row.complete, 'hide':!row.found}">
								<!-- number -->
								<td scope="row">{{row.id+1}}</td>
								<!-- Pay period start -->
								<td v-bind:class="{'complete':row.complete}">{{row.pp_start}}</td>
								<!-- Pay period end -->
								<td v-bind:class="{'complete':row.complete}">{{row.pp_end}}</td>
								<!-- Pay date -->
								<td v-bind:class="{'complete':row.complete,'bg-warning':row.active && !row.complete}">
									<strong>{{row.pay_date}}</strong>
								</td>
								<!-- Earinings on pay date Income -->
								<td class="income" v-bind:class="{'complete':row.complete, 'in-edit':row.id == editing.id && editing.entry_code == 1 && editing.yes && !row.complete}"
									@click="startEdit(row.id, 1, row.income)" v-on:mouseleave="cancelEdit(row, 1)">
									<span>{{formatMoney(row.income)}}</span>
									<span v-if="row.id == editing.id && editing.entry_code == 1 && !row.complete"
										@keyup.esc="cancelEdit(row, 1)" @keyup.enter="doneEdit(row, 1)">
										<Currencyinput :currency="currency" 
										class="edit_inc editable" type="text" 
										v-model="row.income">
										</Currencyinput>
									</span>
								</td>
								<!-- Spendings before pay date -->
								<td v-bind:class="{'complete':row.complete}">
									{{formatMoney(row.spendings)}}
								</td>
								<!-- Savings as of each pay date (expected) -->
								<td class="expected-savings" v-bind:class="{'complete':row.complete, 'has-note':row.note.trim().length>1,
									'in-edit':row.id == editing.id && editing.entry_code == 4 && !row.complete && editing.yes}"
									@click="startEdit(row.id, 4, row.note)" v-on:mouseleave="cancelEdit(row, 4)">
									<span v-bind:class="{'text-danger':row.expected_savings<0}">{{formatMoney(row.expected_savings)}} </span>

									<span class="note" @click.stop="viewNote(row)"><i class="fa fa-eye"></i></span>
									<span class="check-done" @click.stop="doneEdit(row, 4)"><i class="fa fa-save"></i></span>
									<span v-if="!editing.yes" class="delete-note" @click.stop="deleteNote(row.id)"><i class="fa fa-trash"></i></span>

									<textarea v-show="row.id == editing.id && editing.entry_code == 4 && !row.complete"
										class="form-group edit_es editable" @keyup.esc="cancelEdit(row, 4)" 
										name="note-area" :id="'ipt-note'+row.id" cols="40" rows="20" v-model="row.note" placeholder="Write note here">
									</textarea>
								</td>
								<!-- Savings as of each pay date (actual)-->
								<td class="actual-savings" v-bind:class="{'in-edit':row.id == editing.id && editing.entry_code == 2 && editing.yes && !row.complete}"
									@click="startEdit(row.id, 2, row.actual_savings)" v-on:mouseleave="cancelEdit(row, 2)">

									<strong v-if="row.actual_savings>=0">{{formatMoney(row.actual_savings)}}</strong>
									<strong class="text-danger" v-if="row.actual_savings<0">In debt!</strong>

									<span v-if="row.id == editing.id && editing.entry_code == 2 && !row.complete"
										class="form-group" @keyup.esc="cancelEdit(row, 2)" @keyup.enter="doneEdit(row, 2)">
										<Currencyinput :currency="currency"
										class="edit_as editable" type="text"
										v-model="row.actual_savings">
										</Currencyinput>
									</span>
								</td>
								<!-- RRSP (expected) -->
								<td>
									{{formatMoney(row.expected_rrsp)}}
								</td>
								<!-- RRSP (actual) -->
								<td class="actual-rrsp" v-bind:class="{'in-edit':row.id == editing.id && editing.entry_code == 3 && editing.yes && !row.complete}"
									@click="startEdit(row.id, 3, row.actual_rrsp)" v-on:mouseleave="cancelEdit(row, 3)">
									<strong>{{formatMoney(row.actual_rrsp)}}</strong>
									<span v-if="row.id == editing.id && editing.entry_code == 3 && !row.complete"
										@keyup.esc="cancelEdit(row, 3)" @keyup.enter="doneEdit(row, 3)">
										<Currencyinput :currency="currency" 
										class="edit_rrsp editable" type="text" 
										v-model="row.actual_rrsp">
										</Currencyinput>
									</span>
								</td>
								<!-- NOTE -->
								<!-- <td>
									<textarea name="note" id="ipt-note" cols="20" rows="1"></textarea><br>
									<button class="btn btn-secondary btn-sm float-left">&#10003;</button>
								</td> -->
								<!-- Mark done button-->
								<td>
									<button class="btn btn-secondary p-1" @click="markComplete(row.id)">{{row.btn_reads}}</button>
								</td>
							</tr>
						</tbody>
					</table>

					<!-- pagination -->
					<div class="pages text-center p-3 overflow-hidden" v-if="!searching">
						<div>
							<button v-bind:class="{active:single_view.start==0}" class="btn btn-secondary p-2 mr-3"
							@click="resetView(0)">Start</button>
							<button :disabled="!(single_view.start>0)" class="btn btn-secondary p-2 mr-3"
								@click="preView(single_view.start)">&#x2190;</button>

							<button v-for="item in pagination.slice(pagination_start, pagination_end)" :key="item"
							class="btn btn-secondary p-2 mr-3"
							v-bind:class="{active: item==single_view.start}" @click="thisView(item)">{{item/10+1}}</button>

							<button :disabled="!(single_view.end<tracked_data.length-1)" class="btn btn-secondary p-2 mr-3"
							@click="nextView(single_view.end)">&#x2192;</button>
							<button v-bind:class="{active:single_view.end>=tracked_data.length}" class="btn btn-secondary p-2 mr-3"
								@click="resetView(tracked_data.length)">End</button>
						</div><br>
						<div>
							<button v-bind:class="{active: current_item==single_view.start}" class="btn btn-secondary p-2 mr-3"
								@click="currentView(current_item)">Current</button>
						</div>
					
					</div>
					<!-- end pagination -->
				</div>

				<div v-if="tab_isActive[1]">
					<AverageCalc :tracked_data="tracked_data" :user_object="user_object" @ot_created="receivedOt"/>
				</div>

				<div v-if="tab_isActive[2]">
					Graph
					<GChart
						type="LineChart"
						:data="chartData"
					/>
				</div>
			</div>
		</div>

		<back-to-top bottom="50px" right="50px" visibleoffset="400">
			<button type="button" class="btn-dark btn-to-top"><i class="fa fa-chevron-up"></i></button>
		</back-to-top>
	</div>
</div>
</template>

<script>
// @ is an alias to /src
import Currencyinput from '@/components/CurrencyInput.vue'
import AverageCalc from '@/components/AverageCalc.vue'
import db from './firebaseInit'

export default {
	components: {
		Currencyinput,
		AverageCalc
	},
	name:'tracker',
	props:[
		"user_object",
		"initateEdit",
		"restart"
	],
	data() {
		return {
			today: this.formatDate(new Date()),
			//----------------------------------------------
			//---------------------------USER--------------
			firstname: this.user_object.firstname,
			lastname: this.user_object.lastname,

			//----------------------------------------------
			//---------------------------Money--------------
			currency: this.user_object.money.currency,
			starting_savings:  this.user_object.money.starting_savings,
			starting_rrsp:  this.user_object.money.starting_rrsp,
			rrsp_cont:  this.user_object.money.rrsp_cont,
			income_aftertax:  this.user_object.money.income_aftertax,
			rent:  this.user_object.money.rent,
			monthly_payments:  this.user_object.money.monthly_payments,
			weekly_spendings:  this.user_object.money.weekly_spendings,
			//----------------------------------------------
			//---------------------------DATES--------------
			date_interval: this.user_object.dates.date_interval, // the frequency of payment, 14 days means biweekly
			start_date: this.formatDate(this.user_object.dates.start_date),//date of first pay check
			date_array: this.formatDate(this.user_object.dates.date_array), // array of dates
			date_range: this.user_object.dates.date_array.length, // date range is the number of pay periods between start and end dates
			end_date: this.formatDate(this.user_object.dates.end_date), // declare end date variable for projected end date of contract
			hasEndDate: this.user_object.dates.hasEndDate,// boolean to hide or show end date datepicker,

			//----------------------------------------------
			//---------------------------UTILITY--------------
			projected_savings: 0,
			mark_all: {text:'Mark all complete', done:true}, //mark all complete button functionality
			rows_per_view: 10,//number of rows per view for pagination
			pages_per_view:5, //number of pagination buttons per view 
			//preset pagination array slice indexes
			pagination_start: 0,
			pagination_end:5,
			// editing fields:
			editing: {entry_code:0, id:-1, yes:false},
			cached_value:null,
			// cahced_tracked_data: null, //this is an array of all changes in tracked data
			new_tracked_data: null,// this is an array to hold the new value of tracked data after a change,
			state_id:0,// this is used to keep track of the changes array
			filter_term:'',//initiate filter term
			no_results:false,// initiate no results boolean, which is set to false when no results are found
			searching:false,// the current state if user is searching or not
			// Array will be automatically processed with visualization.arrayToDataTable function
      chartData: [
        ['Pay Date', 'Expected savings', 'Actual savings'],
			],
      chartOptions: {
        chart: {
          title: 'Savings over time',
					subtitle: 'Savings since '+this.formatDate(this.user_object.dates.start_date),
					legend: 'left',
					height: 500,
        }
      }
		}
	},
	created() {
		db.collection('user_object').get().then(querySnapshot => {
			querySnapshot.forEach(doc => {
				console.log(doc.id);
				const data = {

				}
			})
		})
		//this will mark all dates complete that fall before the active date
		this.markComplete(-1);
		//this emits an event telling parent componenet "Home.vue" to receive tracked data, so it can be sent to MajorInput componenet so it is sent back in case it's in edit mode
		this.$emit('sendTrackedData', this.tracked_data);
		// draw chart
		this.drawChart();
  },
	computed:{
		current_pp(){
			return this.whatPayPeriod(this.next_paydate, true);
		},
		next_paydate(){
			// console.log(this.$moment(this.today).diff(this.$moment(this.date_array[this.date_array.length-1]), 'days'));
			//if first date in array is after current date or is same as current date, then return first date
			if(this.$moment(this.today).isSameOrBefore(this.date_array[0])){
				return this.date_array[0];
			}
			else { // if not
				if(this.hasEndDate){//if project has an end date
					if(!this.project_hasEnded){//make sure project hasn't already ended
						var dummy_array = [new Date(this.today).getTime()]; //create new dummy array and add today's date to it and convert to miliseconds to sort later
						for(var k=0; k<this.date_array.length; k++){// loop through date array and add all dates to dummy array that already has todays date in it
							dummy_array.push(new Date(this.date_array[k]).getTime()); // convert to miliseconds to sort later
						}
						// sort the array, then get the index of today's date in it. The index of the next date is the same as the current date index in the date array and +1 in the dummy array
						dummy_array.sort();
						//convert back to readable format	from miliseconds 
						for(var j = 0; j<dummy_array.length;j++){
							dummy_array[j] = this.formatDate(dummy_array[j]);
						}
						// return this.date_array[dummy_array.indexOf(this.today)];
						//or 
						return dummy_array[dummy_array.indexOf(this.today)+1];
					}
				}
				else {//if project doesn't have an end date
					return this.getNextPaydates();
				}
			}
		},
		payday_isToday(){
			if(this.date_array.indexOf(this.today) > -1){
				return true;
			}
			else {
				return false;
			}
		},
		project_hasEnded(){
			if(this.hasEndDate && this.end_date){
				if(this.$moment(this.today).isAfter(this.end_date)){
					return true;
				}
				else{
					return false;
				}
			}
			else{
				return false;
			}
		},
		last_pay_passed(){
			if(this.hasEndDate && this.end_date && this.project_hasEnded){
				if(this.$moment(this.today).isAfter(this.date_array[this.date_array.length-1])){
					return true;
				}
				else{
					return false;
				}
			}
			else{
				return false;
			}
		},
		last_pay_amount(){
			if(this.hasEndDate){
				var per_day = this.income_aftertax/this.date_interval; //calc pay per day
				var last_paydate = this.date_array[this.date_array.length-1]; // store the last pay date
				var diff_days = 0; // initiate difference in days between project end date and the start date of the last pay period
				// check if end date falls in the last pay period
				if(this.$moment(this.end_date).isBetween(this.whatPayPeriod(last_paydate).start, this.whatPayPeriod(last_paydate).end)){
					diff_days = this.$moment(this.end_date).diff(this.$moment(this.whatPayPeriod(last_paydate).start), 'days'); //get the day difference
					// below code omits weekend days to accurately calcualte last pay amount
					var weekend_days = 0;//initiate weekend days to 0
					for(var w=0;w<diff_days+1;w++){//loop through the dates between the start of the last pay period and the project end date
						var thisdate = new Date(this.formatDate(this.$moment(this.whatPayPeriod(last_paydate).start).add(w, 'days')));//get each day
						if(thisdate.getDay() == 0 || thisdate.getDay() == 6) weekend_days++;//increase weekend-days by one for every Sunday or Saturday
					}

					return parseFloat(((diff_days-weekend_days)*per_day).toFixed(2)); //return (diff in days - weekend days) X pay per day
				}
				else{
					return this.income_aftertax;
				}
			}
			else{//if not end date then just return the full amount
				return this.income_aftertax;
			}
		},
		days_till(){
			// calculate day difference
			var day_diff = this.$moment(this.next_paydate).diff(this.today, 'days'); // get difference in days
			if(day_diff == 1) return day_diff + ' day';
			else return day_diff + ' days';
		},
		// all data 
		tracked_data:{
			get: function(){
				if(this.new_tracked_data){ //check if this is a set function 
					var hold = this.new_tracked_data; // temporarily hold new tracked data array so it's not mutated
					this.new_tracked_data = null; // nullify array
					return hold; // return the new array
				}
				else{// if not, then just call the function as usual
					return this.getTrackedData();
				}
			},
			set: function(newValue){
				this.new_tracked_data = newValue;//get data property new tracked data array and set it to new array value

			}
		},
		// single view and pagination
		single_view(){
			var view = {start:0, end: this.rows_per_view};//initiate single view
			var active_index = this.tracked_data.map(function(e) { return e.active; }).indexOf(true);//get index of active paydate
			while(active_index>=view.end){//as long as the index of active paydate is less than the initiated end view keep adding the number of rows
				view.start +=this.rows_per_view;
				view.end +=this.rows_per_view;
			}
			
			// the above code defaults to the correct view that corresponds to the active paydate index
			return view;
		},
		pagination(){
			var array = [];//declare array
			for(var b=0; b<this.tracked_data.length/this.rows_per_view; b++){//array length is dictated by number of rows per view and length of date array
				array.push(b*this.rows_per_view);//push item multiplied by rows per view, so that each item in arrayrepresents the index of the first item in every single view
			}
			return array.slice(1,-1); //get rid of first and last elements
		},
		page_remainder(){
			return this.pagination.length%this.pages_per_view; //get remainder of dividing the pagination array length by how many buttons we show per view
		},
		current_item(){//get current item to know current view
			var active_index = this.tracked_data.map(function(e) { return e.active; }).indexOf(true);//get index of active paydate
			while(active_index%10!=0){//keep going back until you hit the first number divisable by 10, that is the start number
				active_index--;
			}
			return active_index; //return he start number
		},
		// end single view and pagination

		ot_expenses () {
			return this.receivedOt();
		},

		tab_isActive(){
			return [true, false, false]
		}
	},
	methods:{
		// test
		test(f){
			switch (f){
				case 1:
				this.today = this.formatDate(this.$moment(this.today).add(120,'days'));
				break;
				case 2:
				this.today = this.formatDate(new Date());
				break;
			}
		},
		receivedOt(ot_expenses){
			return ot_expenses;
		},
		getTrackedData(){
			// declare variables
			var calculated_spendings = 0;
			var table_row={};
			var tracked_data=[];

			//check if user is coming from edit mode
			if(this.user_object.tracked_data.length > 0){ //if yes
				tracked_data = this.user_object.tracked_data; // store data for ease of use
				var start_at = 0;//this will be the value from where all the changes apply

				//get the index of the active row and store it in start_at, then loop through tracked_data starting from that row
				start_at = tracked_data.map(function(e) { return e.active; }).indexOf(true);

				for(var a=start_at+1; a<this.date_array.length; a++){//loop through 
					calculated_spendings = this.calculateSpendings(this.whatPayPeriod(this.date_array[a]).start, this.whatPayPeriod(this.date_array[a]).end);
						tracked_data[a].income=parseFloat(this.income_aftertax.toFixed(2));
						tracked_data[a].spendings= calculated_spendings;
						tracked_data[a].expected_savings= parseFloat((tracked_data[a-1].expected_savings + this.income_aftertax - calculated_spendings).toFixed(2));
						tracked_data[a].actual_savings= parseFloat((tracked_data[a-1].expected_savings + this.income_aftertax - calculated_spendings).toFixed(2));
						tracked_data[a].expected_rrsp= parseFloat((tracked_data[a-1].expected_rrsp + this.rrsp_cont).toFixed(2));
						tracked_data[a].actual_rrsp= parseFloat((tracked_data[a-1].expected_rrsp + this.rrsp_cont).toFixed(2));

					//if this is last entry, then make sure to return the computed last pay amount instead of full income
					if(a==this.date_array.length-1){
						tracked_data[a].income =  parseFloat(this.last_pay_amount.toFixed(2)); 
						this.projected_savings = tracked_data[a].expected_savings;
					}
				}

				return tracked_data;
			}
			else{//if no
				//calculate spendings 
				calculated_spendings = this.calculateSpendings(this.whatPayPeriod(this.date_array[0]).start, this.whatPayPeriod(this.date_array[0]).end);
				//initiate first row
				table_row = {
					id:0,
					pp_start: this.whatPayPeriod(this.date_array[0]).start,
					pp_end: this.whatPayPeriod(this.date_array[0]).end,
					pay_date: this.date_array[0],
					income:parseFloat(this.income_aftertax.toFixed(2)),
					spendings:calculated_spendings,
					expected_savings:parseFloat((this.starting_savings + this.income_aftertax - calculated_spendings).toFixed(2)),
					note: '',
					actual_savings:parseFloat((this.starting_savings + this.income_aftertax - calculated_spendings).toFixed(2)),
					expected_rrsp:parseFloat((this.starting_rrsp + this.rrsp_cont).toFixed(2)),
					actual_rrsp:parseFloat((this.starting_rrsp + this.rrsp_cont).toFixed(2)),
					complete:false,
					btn_reads:'Mark complete',
					active:this.isActiveRow(this.date_array[0], this.next_paydate),
					found:true
				};
				tracked_data = [table_row];//declare array
				for(var a=1; a<this.date_array.length; a++){
					calculated_spendings = this.calculateSpendings(this.whatPayPeriod(this.date_array[a]).start, this.whatPayPeriod(this.date_array[a]).end);
					table_row = {
						id : a,
						pp_start: this.whatPayPeriod(this.date_array[a]).start,
						pp_end: this.whatPayPeriod(this.date_array[a]).end,
						pay_date: this.date_array[a],
						income:parseFloat(this.income_aftertax.toFixed(2)),
						spendings: calculated_spendings,
						expected_savings: parseFloat((tracked_data[a-1].expected_savings + this.income_aftertax - calculated_spendings).toFixed(2)),
						note: '',
						actual_savings: parseFloat((tracked_data[a-1].expected_savings + this.income_aftertax - calculated_spendings).toFixed(2)),
						expected_rrsp: parseFloat((tracked_data[a-1].expected_rrsp + this.rrsp_cont).toFixed(2)),
						actual_rrsp: parseFloat((tracked_data[a-1].expected_rrsp + this.rrsp_cont).toFixed(2)),
						complete:false,
						btn_reads:'Mark complete',
						active:this.isActiveRow(this.date_array[a], this.next_paydate),
						found:true
					};

					//if this is last entry, then make sure to return the computed last pay amount instead of full income
					if(a==this.date_array.length-1){
						table_row.income = this.last_pay_amount;
						this.projected_savings = table_row.expected_savings;
					}
					tracked_data.push(table_row);//add rows based on previous rows
				}

				return tracked_data;
			}
		},
		formatDate(date){
			if(date){
				if(Array.isArray(date)){
					for(var a=0;a<date.length;a++){
						date[a] = this.$moment(date[a]).format('DD/MMM/YYYY');
					}
					return date;
				}
				else{
					return this.$moment(date).format('DD/MMM/YYYY');
				}
			}
			else{
				console.log(date, " no end date has been selected");
			}
		},
		formatMoney(money){
      return this.currency + money.toFixed(2).replace(/\d(?=(\d{3})+\.)/g, '$&,')
    },
		whatPayPeriod(date, current){
			if(typeof current === 'undefined') current = false;//check if we're getting current pay period or pay period of a date
			var pay_period = {
				start:'',
				end:''
			};
			if(date !== ''){//if there's a next pay date
				if(this.date_interval >=28){//if monthly
					pay_period.start = this.formatDate(this.$moment(date).subtract(1, 'months'));//subtract exactly one month
					pay_period.end = this.formatDate(this.$moment(date).subtract(1, 'days'));//until this coming pay date -1

					if(current){
						// check if today's date is before the next pay date but also after the end of the last pay period, then calculate the pay period of the nex next pay date
						if(this.$moment(this.today).isSameOrBefore(date) && this.$moment(this.today).isAfter(pay_period.end)){
							var next_next_paydate = this.$moment(date).add(1, 'months');
							pay_period.start = this.formatDate(this.$moment(next_next_paydate).subtract(1, 'months'));//subtract exactly one month
							pay_period.end = this.formatDate(this.$moment(next_next_paydate).subtract(1, 'days'));//until this coming pay date -1
						}
					}
				}
				else if (this.date_interval == 14){//if biweekly
					pay_period.start = this.formatDate(this.$moment(date).subtract(this.date_interval+5, 'days'));
					pay_period.end = this.formatDate(this.$moment(date).subtract(this.date_interval-8, 'days'));

					if(current){
						// check if today's date is before the next pay date but also after the end of the last pay period, then calculate the pay period of the nex next pay date
						if(this.$moment(this.today).isSameOrBefore(date) && this.$moment(this.today).isAfter(pay_period.end)){
							var next_next_paydate = this.$moment(date).add(this.date_interval, 'days');
							pay_period.start = this.formatDate(this.$moment(next_next_paydate).subtract(this.date_interval+5, 'days'));
							pay_period.end = this.formatDate(this.$moment(next_next_paydate).subtract(this.date_interval-8, 'days'));
						}
					}
				}
				else if (this.date_interval == 7){//if weekly
					pay_period.start = this.formatDate(this.$moment(date).subtract(this.date_interval, 'days'));
					pay_period.end = this.formatDate(this.$moment(date).subtract(this.date_interval-6, 'days'));

					if(current){
						// check if today's date is before the next pay date but also after the end of the last pay period, then calculate the pay period of the nex next pay date
						if(this.$moment(this.today).isSameOrBefore(date) && this.$moment(this.today).isAfter(pay_period.end)){
							var next_next_paydate = this.$moment(date).add(this.date_interval, 'days');
							pay_period.start = this.formatDate(this.$moment(next_next_paydate).subtract(this.date_interval, 'days'));
							pay_period.end = this.formatDate(this.$moment(next_next_paydate).subtract(this.date_interval-6, 'days'));
						}
					}
				}
			}
			return pay_period;
		},
		calculateSpendings(date_1, date_2){
			date_1 = new Date(date_1); //get first date in miliseconds
			date_2 = new Date(date_2); //get second date in miliseconds

			if(date_1.getMonth() == date_2.getMonth()){//if they are the same month 
				if(date_1.getDate() == 1 || date_2.getDate() ==1){//make sure none of them start or end with the first day of that month
					//if they do start or end with the first day of the month then add all monthly expenses to weekly expenses
					return parseFloat(((this.weekly_spendings/7*this.date_interval)+this.rent+this.monthly_payments).toFixed(2));
				}
				else{//if not return only weekly spendings
					return parseFloat((this.weekly_spendings/7*this.date_interval).toFixed(2));
				}
			}
			else{//if they are not the same month then add all monthly expenses to weekly expenses
				return parseFloat(((this.weekly_spendings/7*this.date_interval)+this.rent+this.monthly_payments).toFixed(2));
			}
		},
		getNextPaydates(){
			//check if today's date is same or before the end date of the pay period of the last pay date in the array
			if(this.$moment(this.today).isSameOrBefore(this.whatPayPeriod(this.date_array[this.date_array.length-1]).end)){
				return this.date_array[this.date_array.length-1];
			}
			else{//if it's after then add the nextpay date to the array because now you're in a new pay period
				var days_passed = this.$moment(this.today).diff(this.$moment(this.date_array[this.date_array.length-1]), 'days');
				var nextpaydate = '';
				if(days_passed > this.date_interval){
					for(var d=0; d<Math.ceil(days_passed/this.date_interval); d++){
						if(this.date_interval<28){//if it's weekly or biweekly just add the interval since it's a constant (7 or 14)
							nextpaydate = this.formatDate(this.$moment(this.date_array[this.date_array.length-1]).add(this.date_interval, 'days'));
							this.date_array.push(nextpaydate);
						}
						else{//however, if it's monthly then the date interval is set to the number of days of the following month (meaning that if it's month N, the next pay date is (N + (N+1 days))
							nextpaydate = this.formatDate(this.$moment(this.date_array[this.date_array.length-1]).add(1, 'months'));
							this.date_array.push(nextpaydate);
						}
					}
				}
				else{
					if(this.date_interval<28){//if it's weekly or biweekly just add the interval since it's a constant (7 or 14)
						nextpaydate = this.formatDate(this.$moment(this.date_array[this.date_array.length-1]).add(this.date_interval, 'days'));
						this.date_array.push(nextpaydate);
					}
					else{//however, if it's monthly then the date interval is set to the number of days of the following month (meaning that if it's month N, the next pay date is (N + (N+1 days))
						nextpaydate = this.formatDate(this.$moment(this.date_array[this.date_array.length-1]).add(1, 'months'));
						this.date_array.push(nextpaydate);
					}
				}
				return nextpaydate;
			}
		},
		markComplete(id){
			if(id>=0){//if it's a valid id i.e equal or greater than 0
				if(this.$moment(this.tracked_data[id].pay_date).isSameOrBefore(this.next_paydate)){//make sure it's not a future date
					this.tracked_data[id].complete = !this.tracked_data[id].complete;
					if(this.tracked_data[id].complete)this.tracked_data[id].btn_reads='Undo';
					else this.tracked_data[id].btn_reads='Mark complete';
				}
				else{//if future date return error
					this.$swal.fire({
						position: 'center',
						text: "Can't mark complete future dates!",
						confirmButtonText:'Got it!',
						buttonsStyling:false,
						confirmButtonClass:'btn btn-secondary btn-small'
					})
				}
			}
			else{//otherwize then it's the mark all complete button that has been pressed, since we're passing -1
				//get the index of the active row and store it in active_index, then loop through tracked_data only until that row because future dates can't be marked complete
				var active_index = this.tracked_data.map(function(e) { return e.active; }).indexOf(true);
				if(this.mark_all.done){//first time clicked we're marking all complete, which is why the default value is true
					// go through and change all relevant data
					for(var c=0;c<active_index;c++){
						this.tracked_data[c].complete=true;
						this.tracked_data[c].btn_reads='Undo';
						this.mark_all.done=false;
						this.mark_all.text='Undo all';
					}
				}
				else {//if undo is pressed
					// go through and change back all relevant data
					for(var c=0;c<this.tracked_data.length;c++){
						this.tracked_data[c].complete=false;
						this.tracked_data[c].btn_reads='Mark complete';
						this.mark_all.done=true;
						this.mark_all.text='Mark all complete';
					}
				}
			}
			this.$forceUpdate();
		},
		isActiveRow(date1, date2){//check if current row is the active one by comparing its pay date with the next pay date
			if(this.$moment(date1).isSame(date2))return true;
			else return false;
		},
		// pagination
		nextView(view_end){
			// set the slice indexes for pagination array
			// get the index of the passed view and make sure it's greater than 0 and also is divisble by the number of pagination buttons per view
			if(this.pagination.indexOf(view_end)%this.pages_per_view == 0 && this.pagination.indexOf(view_end)/this.pages_per_view>0){ 
				// now check that there enough elements in the pagination array to sustain the number of pagination buttons per view
				if(this.pagination.length-this.pagination.indexOf(view_end) > this.pages_per_view){
					this.pagination_start+=this.pages_per_view;
					this.pagination_end+=this.pages_per_view;
				}
				else{// if not, make sure to add only the remainder when setting pagination array slice indexes
					this.pagination_start+=this.pages_per_view;
					this.pagination_end+=this.page_remainder;
				}
			}
			
			// set slice indexes for table rows
			this.single_view.start=view_end;
			this.single_view.end+=this.rows_per_view;
			this.$forceUpdate();
		},
		preView(view_start){
			// set the slice indexes for pagination array
			// get the index of the passed view and make sure it's greater than 0 and also is divisble by the number of pagination buttons per view
			if(this.pagination.indexOf(view_start)%this.pages_per_view == 0 && this.pagination.indexOf(view_start)/this.pages_per_view>0){ 
				// now check that there are enough elements in the pagination array to sustain the number of pagination buttons per view
				if(this.pagination.length-this.pagination.indexOf(view_start) > this.pages_per_view){
					this.pagination_start-=this.pages_per_view;
					this.pagination_end-=this.pages_per_view;
				}
				else{// if not, make sure to add only the remainder when setting pagination array slice indexes
					this.pagination_start-=this.pages_per_view;
					this.pagination_end-=this.page_remainder;
				}
			}

			// set slice indexes for table rows
			this.single_view.end=view_start;
			this.single_view.start-=this.rows_per_view;
			this.$forceUpdate();
		},
		resetView(num){
			//if start is pressed, num is 0 but if end is pressed num is same as the length of the tracked_data array
			if(num==0){//if start pressed
				// set slice indexes for table rows
				this.single_view.start=num; //start from 0
				this.single_view.end=this.rows_per_view;//end is just rows per view from start 
				
				// set the slice indexes for pagination array
				this.pagination_start= 0;
				this.pagination_end=5;

			}
			else if (num==this.tracked_data.length){//else if end pressed
				// set slice indexes for table rows
				var last_view = this.tracked_data.length%this.rows_per_view;//get the remainder to know how many rows are in the last view
				//start at length of array minus remainder if remainder isn't 0, if it is then minus rows per view
				if(last_view!=0)this.single_view.start=num-last_view; 
				else this.single_view.start=num-this.rows_per_view;
				this.single_view.end=num; //ends at length of array

				// set the slice indexes for pagination array
				this.pagination_end=this.pagination.length;
				this.pagination_start=this.pagination_end-this.page_remainder;
			}
			this.$forceUpdate();
		},
		thisView(item){
			// set slice indexes for table rows
			this.single_view.start=item; //start with item value
			this.single_view.end=item+this.rows_per_view;//ends with item value plus rows per view
			this.$forceUpdate();
		},
		currentView(item){
			var d = (item/10)-5;
			if(d<0) d = 0;
			// set the slice indexes for pagination array
			this.pagination_start= d; 
			//check if it's the last pagination page by making sure more than 5 pagination buttons still remain 
			if(this.pagination.length - this.pagination_start > 5) this.pagination_end=((item/10)-1)+4; 
			else this.pagination_end=(item/10-1)+this.page_remainder; // if not then add only the remainder

			this.thisView(item);
		},
		dataView(){
			//this is the function that determines the current view 
			if(this.searching){//if user is searching for something, disable this functionality by showing all entries
				return this.tracked_data.slice(0, this.tracked_data.length);
			}
			else{ // if not, then default it to whatever the value is for the computed property single_view
				return this.tracked_data.slice(this.single_view.start, this.single_view.end);
			}
		},
		//end pagination

		// edit entries
		startEdit(id, entry, value) {
			//verify the row by checking row id and entry code 
			if(this.editing.id == id && this.editing.entry_code == entry){
				this.editing.yes = true;//now in editing mode
			}
			else { // if not then
				this.editing.yes = false; // not in editing mode
				//set entry code to passed code and id to passed id so now it passed when clicked again (double click functionality)
				this.editing.entry_code = entry; 
				this.editing.id = id;
			}
			//set cached value to the passed value, this is important so the original value isn't lost or altered and cahced value is used later
			this.cached_value = value;
    },
    doneEdit(row, entry) {
			//reset the editing object
      this.editing.entry_code = 0;
			this.editing.id = -1;
			this.editing.yes = false;

			//update row in question
			this.tracked_data[row.id] = row;
			var diff = 0;//initiate difference

			if(entry==4){//if it is the note, make sure not to accept spaces 
				if(row.note.trim().length == 0){
					this.tracked_data[row.id].note = '';
				}
			}

			switch(entry){//calculate the difference based on entry
				case 1://income
					diff = parseFloat((this.tracked_data[row.id].income - this.cached_value).toFixed(2));
				break;
				case 2://actual savings
					diff = parseFloat((this.tracked_data[row.id].actual_savings - this.cached_value).toFixed(2));
					
					this.tracked_data[row.id].spendings = parseFloat((this.tracked_data[row.id].expected_savings - this.tracked_data[row.id].actual_savings).toFixed(2))
					if(this.tracked_data[row.id].spendings < 0) this.tracked_data[row.id].spendings = 0;

				break;
				case 3://actual rrsp
					diff = parseFloat((this.tracked_data[row.id].actual_rrsp - this.cached_value).toFixed(2));
				break;
			}

			//if the edited field is actual savings or actual rrsp, then update all rows after current row to reflect the difference throughout the tracked data array
			for(var a=row.id+1; a<this.tracked_data.length; a++){
				if(entry==1){//if income then update current savings and future ones both actual and expected 
					parseFloat((this.tracked_data[a-1].actual_savings+=diff).toFixed(2));
					parseFloat((this.tracked_data[a].actual_savings+=diff).toFixed(2));
					parseFloat((this.tracked_data[a].expected_savings+=diff).toFixed(2));
				}
				else if(entry==2){ //update future savings both actual and expected 
					parseFloat((this.tracked_data[a].actual_savings+=diff).toFixed(2));
					parseFloat((this.tracked_data[a].expected_savings+=diff).toFixed(2));
				}
				else if(entry==3){//update future rrsp both actual and expected 
					parseFloat((this.tracked_data[a].actual_rrsp+=diff).toFixed(2));
					parseFloat((this.tracked_data[a].expected_rrsp+=diff).toFixed(2));
				}
			}

			this.state_id++;//increase state id to keep track of changes
			//push the newly mutated tracked data array to changes array
			// this.changes.push(this.tracked_data);
			// console.log('change num '+ this.state_id, this.changes[this.state_id][row.id].income);

			// console.log("length ", this.changes.length)
			// console.log('original ', this.changes[0][10].income)
			// console.log('change number '+this.state_id,': ', this.changes[this.state_id][10].income)
    },
    cancelEdit(row, entry) {
			if(this.editing.yes){//make sure it's in editing mode
				switch(entry){//if canceled revert to cached value depending on entry code
					case 1://income
						this.tracked_data[row.id].income = this.cached_value;
					break;
					case 2://savings
						this.tracked_data[row.id].actual_savings = this.cached_value;
					break;
					case 3://rrsp
						this.tracked_data[row.id].actual_rrsp = this.cached_value;
					break;
					case 4://note
						this.tracked_data[row.id].note = this.cached_value;
					break;
				}

			}
			this.editing={entry_code:0, id:-1, yes: false};//reset editing object
		},
		viewNote(row){
			//fire swal alert with note content
			this.$swal.fire({
				position: 'center',
				text: row.note,
				confirmButtonText:'Ok',
				buttonsStyling:false,
				confirmButtonClass:'btn btn-secondary btn-small'
			})
		},
		deleteNote(id){
			//fire swal alert to make sure user wants to delete note
			this.$swal.fire({
				text: "Are you sure you want to delete note?",
				type: 'warning',
				showCancelButton: true,
				buttonsStyling:false,
				confirmButtonClass:'btn btn-secondary btn-small mr-3',
				cancelButtonClass: 'btn btn-secondary btn-small',
				confirmButtonText: 'Yes'
			}).then((result) => {
				if (result.value) {
					this.tracked_data[id].note='';//reset note if confirmed
				}
			})
		},	
		giveDetails(msg){
				//give detailes swal alert
				this.$swal.fire({
				position: 'center',
				text: msg,
				confirmButtonText:'Got it!',
				buttonsStyling:false,
				confirmButtonClass:'btn btn-secondary btn-small'
			})
		},
		// end edit entries
		
		// TODO chart
		drawChart(){
			for (var i = 1; i < this.tracked_data.length; i++) {
				if(this.tracked_data[i-1].complete){
					this.chartData[i]=[
						this.tracked_data[i-1].pay_date,
						{v:this.tracked_data[i-1].expected_savings, f:this.formatMoney(this.tracked_data[i-1].expected_savings)},
						{v:this.tracked_data[i-1].actual_savings, f:this.formatMoney(this.tracked_data[i-1].actual_savings)},
					]
				}
			}
		},
		// end TODO chart

		//search/filter functionality
		filterData(term){
			if(term !== ''){// make sure term is not empty
				var result_count = 0;//initialize result counter 
				term = term.toLowerCase();//transofrm to lower case to make it not case sensitive
				// loop through tracked data
				for(var i=0; i<this.tracked_data.length;i++){
					//make sure to reset to default. no result to false, set searching to false and also make all data rows found (default value) 
					this.no_results = false;
					this.searching = false;
					this.tracked_data[i].found = true;
					//end reset

					//get and combine a string of all dates and the note in each row
					var arr = [this.tracked_data[i].note,this.tracked_data[i].pay_date, this.tracked_data[i].pp_start, this.tracked_data[i].pp_end];
					var str = arr.join().toLowerCase();//transofrm to lower case to make it not case sensitive

					if(!str.includes(term)){//check if the term is NOT a substring of the row string
						this.tracked_data[i].found = false;// as long as it is not, then make the row not found
					}
					else {
						result_count++;//keep track of number of results using counter
					}
				}

				if(result_count==0){//if counter is 0, means no results
					this.no_results = true;//set no_results to true
					//and make every row found (reset to default view)
					for(var i=0; i<this.tracked_data.length;i++){
						this.tracked_data[i].found = true;
					}
				}
				else{
					//if there are results, then enter searching mode
					this.searching = true;
				}
			}
		},
		removeFilter(){
			//this just exits searching mode and resets everything
			this.filter_term = '';
			this.searching = false;
			this.no_results = false;
			for(var i=0; i<this.tracked_data.length;i++){
				this.tracked_data[i].found = true;
			}
		},
		switchTab(index){
			if(index > 0){
				this.$swal.fire({
					position: 'center',
					text: 'Coming soon!',
					confirmButtonText:'Ok',
					buttonsStyling:false,
					confirmButtonClass:'btn btn-secondary btn-small'
				})
			}
			else{
				for (let t = 0; t < this.tab_isActive.length; t++) {
				this.tab_isActive[t] = false;
				}
				this.tab_isActive[index] = true;
				this.$forceUpdate();
			}
		}
		
