<template>
	<div>
		<Majorinput :edit_user_object="user_object" :tracked_data="tracked_data" v-if="!inputDone" @inputConfirmed="inputReceived"/>
		<div v-if="inputDone" class="p-2">
			<Tracker :user_object="user_object" :initateEdit="initateEdit" :restart="restart" @sendTrackedData="TrackedDataSent"/>
		</div>
	</div>
</template>

<script>
// @ is an alias to /src
import Majorinput from '@/components/MajorInput.vue'
import Tracker from '@/components/Tracker.vue'

export default {
	name:'main-view',
	components: {
		Majorinput,
		Tracker
	},
	data() {
		return {
			inputDone: false,
			user_object:{},
			tracked_data:[]
		}
	},
	methods: {
		inputReceived(user_object){
			if(user_object){
				this.inputDone = true;
				this.user_object = user_object;
			}
			else{
				this.inputDone = false;
			}
		},
		initateEdit(){
			this.inputDone = false;
		},
		restart(){
			//fire swal alert to make sure user wants to delete note
			this.$swal.fire({
				text: "Are you sure you want to restart? All of your tracked data will be deleted",
				type: 'warning',
				showCancelButton: true,
				buttonsStyling:false,
				confirmButtonClass:'btn btn-secondary btn-small mr-3',
				cancelButtonClass: 'btn btn-secondary btn-small',
				confirmButtonText: 'Yes'
			}).then((result) => {
				if (result.value) {
					//restart  if confirmed
					this.user_object = {}
					this.inputDone = false;
				}
			})
			
		},
		TrackedDataSent(tracked_data){
			if(tracked_data){
				this.tracked_data = tracked_data;
			}
		}
	}
}
</script>