<template>
	<div class="content container p-3">
		<div class="text-center mb-5">
			<p v-if="edit_mode"><strong>EDIT MODE</strong></p>
			<button :disabled="!userValidated" class="btn btn-secondary" @click="clearAll()">Clear all</button>
		</div>
		<div class="user">
			<form>
				<div class="form-row">
					<div class="form-group col-md-6">
						<label for="firstnameipt">First Name</label>
						<input type="text" class="form-control" id="firstnameipt" placeholder="First Name" v-model="firstname">
					</div>
					<div class="form-group col-md-6">
						<label for="countryipt">Last Name</label>
						<input type="text" class="form-control" id="countryipt" placeholder="Last Name" v-model="lastname">
					</div>
				</div>
			</form>
			<!-- <span>Form validated? {{userValidated}}</span> -->
		</div>
		<!-- v-if="computed_date_array.length>0" -->
		<div class="money" v-if="userValidated">  
			<div class="form-row">
				<div class="form-group col-md-1" v-if="!edit_mode">
					<label>Currency</label>
					<select id="csipt" class="form-control" v-model="currency">
						<option v-for="(curr, index) in currency_options" :key="index" :value="curr" :selected="index==0">{{curr}}</option>
					</select>
				</div>
				<div class="form-group col-md-4" v-if="!edit_mode">
					<label>Starting savings <span class="info_block" @click="giveDetails('What you have in savings as of now')">?</span></label>
					<Currencyinput id="ssipt" v-model="starting_savings" :currency="currency"></Currencyinput>
				</div>
				<div class="form-group col-md-4" v-if="!edit_mode">
					<label>Starting RRSP <span class="info_block" @click="giveDetails('What you have in RRSP as of now')">?</span></label>
					<Currencyinput id="srrspipt" v-model="starting_rrsp" :currency="currency"></Currencyinput>
				</div>
				<div class="form-group col-md-3">
					<label>RRSP contribution <span class="info_block" @click="giveDetails('How much RRSP you make every paycheck?')">?</span></label>
					<Currencyinput id="rrspcipt" v-model="rrsp_cont" :currency="currency"></Currencyinput>
				</div>
			</div>
			
			<div class="form-row">
				<div class="form-group col-md-3">
					<label>Income after tax <span class="info_block" @click="giveDetails('The net pay amount on your paycheck')">?</span> </label>
					<Currencyinput id="incomeipt" v-model="income_aftertax" :currency="currency"></Currencyinput>
				</div>
				<div class="form-group col-md-3">
					<label>Rent </label>
					<Currencyinput id="rentipt" v-model="rent" :currency="currency"></Currencyinput>
				</div>
				<div class="form-group col-md-3">
					<label >Monthly payments <span class="info_block" @click="giveDetails('The total of all fixed monthly payments')">?</span></label>
					<Currencyinput id="mpipt" v-model="monthly_payments" :currency="currency"></Currencyinput>
				</div>
				<div class="form-group col-md-3">
					<label >Weekly spendings <span class="info_block" @click="giveDetails('How much do you think you spend a week?')">?</span></label>
					<Currencyinput id="wsipt" v-model="weekly_spendings" :currency="currency"></Currencyinput>
				</div>
			</div>
		</div>

		<div class="dates" v-if="moneyValidated && !edit_mode">
			<form>
				<div class="form-group">
					<label>Select income frequency</label>
					<select id="fsipt" class="form-control" v-model="date_interval">
						<option v-for="(frequency, index) in frequency_options" :key="index" :value="frequency.interval" :selected="index==0">{{frequency.freq}}</option>
					</select>
					<!-- <p>{{date_interval}}</p> -->
				</div>

				<div class="form-group">
					<label>Select the first pay date from which you wish to track</label>
					<datepicker :format="'dd-MMM-yyyy'" id="date_1" placeholder="Please select a starting pay date" v-model="start_date" name="startingdate" :disabled-dates="date_1.disabledDates" :disabled="hasEndDate" bootstrap-styling ></datepicker>
				</div>
				<div v-if="start_date" class="form-check mb-3">
					<input v-on:click="resetDates(hasEndDate)" v-model="hasEndDate" type="checkbox" class="form-check-input" id="HasendDate">
					<label class="form-check-label" for="HasendDate">Check to add end date</label>
				</div>

				<div v-if="date_2" class="form-group" v-bind:class="{ hide: !hasEndDate }">
					<label>If you have a projected end date, please select it below. </label>
					<datepicker :format="'dd-MMM-yyyy'" id="date_2" placeholder="Please select an end pay date" v-model="end_date" name="enddate" :disabled-dates="date_2.disabledDates" bootstrap-styling :open-date='date_2.disabledDates.to' ></datepicker>
				</div>
			</form>

			<div v-if="start_date" class="results">
				<p v-if="end_date"> There is a total of {{computed_date_array.length}} pay days including the starting date selected above</p>
				<!-- <p>Next pay date is {{next_paydate | moment("DD-MMM-YYYY")}}</p> -->
				<!-- <div v-for="(date, index) in computed_date_array" :key="index"> <span>{{date | moment("DD-MMM-YYYY")}} </span></div> -->
				<!-- <div v-if="end_date">{{computed_date_difference}} days</div> -->
			</div>
		</div>
		<br>

		<div class="confirm" v-if="userValidated && moneyValidated && start_date">
			<div v-if="!end_date && !hasEndDate">
				<p class="text-danger">No end date selected, want to confirm and proceed?</p>
				<button type="submit" class="btn btn-secondary" @click="ConfirmInput()">Yes</button>
			</div>
			<button v-if="end_date" type="submit" class="btn btn-secondary" @click="ConfirmInput()">Confirm</button>
		</div>
	</div>
</template>

<script>
// @ is an alias to /src
import Datepicker from 'vuejs-datepicker'
import db from './firebaseInit'
import Currencyinput from '@/components/CurrencyInput.vue'
export default {
	components: {
		Datepicker,
		Currencyinput
	},
	name:'major-input',
	props:{
		edit_user_object: Object,
		tracked_data: Array
	},
	data() {
		return {
			today: this.$moment(new Date()).format('DD-MMM-YYYY'),
			//----------------------------------------------
			//---------------------------USER--------------
			firstname:'Ammar',
			lastname:'Moussa',

			//----------------------------------------------
			//---------------------------Money--------------
			currency:'C$',
			starting_savings:57574.31,
			starting_rrsp:2386.82,
			rrsp_cont:96.5,
			income_aftertax:3123.05,
			rent:1650,
			monthly_payments:488+14+45+45+90+30,
			weekly_spendings:300,
			//----------------------------------------------
			//---------------------------DATES--------------
			// today: new Date('2019-11-10').getTime(),
			date_interval:14, // the frequency of payment, 14 days means biweekly
			start_date:new Date ('01/03/2020'),//date of first pay check
			date_array:[], // array of dates
			date_range:0, // date range is the number of pay periods between start and end dates
			end_date:new Date ('01/24/2020'), // declare end date variable for projected end date of contract
			hasEndDate: true,// boolean to hide or show end date datepicker
			date_1:{ // the disabled dates object for the first date picker (start date) review resource #1
				disabledDates: {
					to: new Date(new Date().setDate(new Date().getDate()-1)), // Disable all dates up to yesterday
					// days: [6, 0], // Disable Saturday's and Sunday's
				}
			},
			// utility data
			currency_options:['C$', 'US$', '', ''],
		}
	},
	// created(){
	// 	db.collection('user_object').get().then(querySnapshot => {
	// 		querySnapshot.forEach(doc => {
	// 			console.log(doc);
	// 			const data = {

	// 			}
	// 		})
	// 	})
	// },
	computed:{
		//----------------------------------------------
		//---------------------------USER--------------
		userValidated(){
			//now validate form, and once we're sure all fields are filled
			if(this.firstname !=='' && this.lastname !==''){
				return true;
			}
			else{
				return false;
			}
		},
		//----------------------------------------------
		//---------------------------MONEY--------------
		moneyValidated(){//validate that at least income and weekly spendings are above 0
			if(this.starting_savings>=0 &&
				this.starting_rrsp>=0 &&
				this.rrsp_cont>=0 &&
				this.income_aftertax>0 &&
				this.rent>=0 &&
				this.monthly_payments>=0 &&
				this.weekly_spendings>0){
					return true;
				}
				else{
					return false;
				}
		},
		//----------------------------------------------
		//---------------------------DATES--------------
		// get the number of days difference between start date and end date
		computed_date_difference(){
			var diff_in_days = 0;
			if(this.end_date){ //only return if end date has been selected and is defined
				// diff_in_days = (this.end_date.getTime()-this.start_date.getTime())/(1000 * 3600 * 24); //calc days difference
				diff_in_days = this.$moment(this.end_date).diff(this.start_date, 'days')+1; //calc days difference
				// console.log(diff_in_days/this.date_interval);
				// perform backup check to ensure no past selection or selection of 2 dates less than 14 days apart (won't happen but just here in case)
				if(this.end_date.getTime()<this.start_date.getTime()){
					alert("End date can't be before start date");
				}
				else{
					if(diff_in_days < this.date_interval){
						alert("End date and start date should be at least "+this.date_interval+" days apart");
					}
					else{
						return diff_in_days;//return difference rounded to nearest int
					}
				}
			}
		},
		// get the dates of every pay date 
		computed_date_array(){
			if(this.start_date){
				this.date_array = [];//reset array if user changes start date mid session
				if(this.end_date){
					//calculate and ceil date range so you get the pay date of the last pay period and all pay dates in between
					this.date_range = Math.ceil(this.computed_date_difference/this.date_interval);
					
					//let's make sure that the end day of the porject doesn't fall outside of the last pay period(for biweekly)
					if(this.date_interval <28){
						var anticipated_lastPayDate = this.$moment(this.start_date).add(this.date_range*this.date_interval, 'days');//get anticipated last pay day
						var last_pp_start = this.$moment(anticipated_lastPayDate).subtract(this.date_interval+5, 'days');//last pay period start
						var last_pp_end = this.$moment(anticipated_lastPayDate).subtract(this.date_interval-8, 'days');//last pay period end
						//now make sure that the end date falls in the last pay period or including start and end dates, if it doesn't increment date_range by 1 to cover the last pay period
						if(!this.$moment(this.end_date).isBetween(last_pp_start, last_pp_end, null, '[]')){
							this.date_range+=1;
						}
					}
					this.date_array = [];//reset array to include end date too now
				}
				else{
					//if today falls after the start date of the project
					if(this.$moment(this.today).isAfter(this.start_date)){
						var since_start = this.$moment(this.today).diff(this.start_date, 'days');// calculate the how many days since project started
						//calculate and ceil date range based on how many days it has been since project started
						this.date_range = Math.ceil(since_start/this.date_interval);
					}
					else{//if today is on or before start date
						this.date_range = 1;//default value of date range if no end date selected
					}
				}

				this.date_array.push(this.start_date);//initiate array with start date
				for(var i=1; i<this.date_range+1; i++){
					this.date_array.push(new Date(this.date_array[i-1]));

					if(this.date_interval<28){//if it's weekly or biweekly just add the interval since it's a constant (7 or 14)
						this.date_array[i].setDate(this.date_array[i-1].getDate() + this.date_interval);
					}
					else{//however, if it's monthly then the date interval is set to the number of days of the following month (meaning that if it's month N, the next pay date is (N + (N+1 days))
						this.date_array[i].setDate(this.date_array[i-1].getDate() + this.daysInMonth(this.date_array[i].getMonth(), this.date_array[i-1].getFullYear()));
					}
				}
			}
			return this.date_array;
		},
		//this computed returns the dates of the end date datepicker
		date_2(){
			if(this.start_date){//only works if start date has been selected
				// console.log(this.start_date);
				// console.log(this.addDays(this.start_date, this.date_interval));
				var date_2= { //disable all days up to start date plus whatever the date interval is
					disabledDates: {
						to: new Date(this.addDays(this.start_date, this.date_interval)), // Disable all dates up to specific date
					}
				}
				return date_2;
			}
		},
		frequency_options(){
			var days_in_month = 30;
			if(this.start_date){// get the number of days in the month of the start date
				days_in_month = this.daysInMonth(this.start_date.getMonth(), this.start_date.getFullYear());
			}
			// else{//otherwise get the number of days in the month of today's date, just as a placeholder
			//   days_in_month = this.daysInMonth(this.today.getMonth(), this.today.getFullYear());
			//   console.log(days_in_month);
			// }

			//update interval value only if selected interval is monthly
			if(this.date_interval >=28){
				this.date_interval = days_in_month;
			}

			return [{freq:'Biweekly', interval: 14}, {freq:'Weekly', interval: 7}, {freq:'Monthly', interval: days_in_month}];
		},
		edit_mode(){
			if(Object.keys(this.edit_user_object).length === 0){//if edit user object passed from tracker is empty, then it's not in edit mode
				return false;
			}
			else {// if it is not empty, then it is edit mode, and get all passed data from edit_user object
				//----------------------------------------------
				//---------------------------USER--------------
				this.firstname= this.edit_user_object.firstname;
				this.lastname= this.edit_user_object.lastname;

				//----------------------------------------------
				//---------------------------Money--------------
				this.currency= this.edit_user_object.money.currency;
				this.starting_savings=  this.edit_user_object.money.starting_savings;
				this.starting_rrsp=  this.edit_user_object.money.starting_rrsp;
				this.rrsp_cont=  this.edit_user_object.money.rrsp_cont;
				this.income_aftertax=  this.edit_user_object.money.income_aftertax;
				this.rent=  this.edit_user_object.money.rent;
				this.monthly_payments=  this.edit_user_object.money.monthly_payments;
				this.weekly_spendings=  this.edit_user_object.money.weekly_spendings;
				//----------------------------------------------
				//---------------------------DATES--------------
				this.date_interval= this.edit_user_object.dates.date_interval; // the frequency of payment; 14 days means biweekly
				this.start_date= this.edit_user_object.dates.start_date;//date of first pay check
				this.date_array= this.edit_user_object.dates.date_array; // array of dates
				this.date_range= this.edit_user_object.dates.date_array.length; // date range is the number of pay periods between start and end dates
				this.end_date= this.edit_user_object.dates.end_date; // declare end date variable for projected end date of contract
				this.hasEndDate= this.edit_user_object.dates.hasEndDate;// boolean to hide or show end date datepicker,

				return true;
			}
		},
		//----------------------------------------------
		//---------------------------FINAL--------------
		user_object(){
			var user_object = {
				firstname: this.firstname,
				lastname: this.lastname,
				money:{
					starting_savings: this.starting_savings,
					starting_rrsp: this.starting_rrsp,
					rrsp_cont: this.rrsp_cont,
					income_aftertax: this.income_aftertax,
					rent: this.rent,
					monthly_payments: this.monthly_payments,
					weekly_spendings:this.weekly_spendings,
					currency:this.currency
				},
				dates:{
					start_date:this.start_date,
					date_array:this.computed_date_array,
					hasEndDate: this.hasEndDate,
					end_date:this.end_date,
					day_difference: this.computed_date_difference,
					date_interval:this.date_interval
				},
				ot_expenses:[],
				tracked_data: []
			};

			
			if(this.edit_mode) user_object.tracked_data= this.tracked_data 

			return user_object;
		},
	},
	methods:{
		resetDates(hasEndDate){
			if(hasEndDate){
				this.end_date='';
				this.date_array=[];
			}
		},
		addDays(date, days){
			const copy = new Date(Number(date))
			copy.setDate(date.getDate() + days)
			return copy;
		},
		daysInMonth(month, year){
			return new Date(year, month+1, 0).getDate();
		},
		giveDetails(msg){
				this.$swal.fire({
				position: 'center',
				text: msg,
				confirmButtonText:'Got it!',
				buttonsStyling:false,
				confirmButtonClass:'btn btn-secondary btn-small'
			})
		},
		ConfirmInput(){
			this.$emit('inputConfirmed', this.user_object);
		},
		clearAll(){
			this.firstname = '';
			this.lastname = '';
			this.starting_savings=0;
			this.starting_rrsp=0;
			this.rrsp_cont=0;
			this.income_aftertax=0;
			this.rent=0;
			this.monthly_payments=0;
			this.weekly_spendings=0;
			this.start_date='';
			this.date_array=[];
			this.hasEndDate=false;
			this.end_date='';
		},
		formatDate(date){
			if(date){
				if(Array.isArray(date)){
					for(var a=0;a<date.length;a++){
						date[a] = this.$moment(date[a]).format('DD-MMM-YYYY');
					}
					return date;
				}
				else{
					return this.$moment(date).format('DD-MMM-YYYY');
				}
			}
			else{
				console.log(date, " no end date has been selected");
			}
		},
	},
}
</script>
